import React from 'react';
import './InfoForm.css';

class InfoForm extends React.Component {
    constructor(props) {
        super(props);

        this.state = {
            submitted: false,
            userEmail: '' // здесь пока пустая строка
        }
    }

    handleSubmit = () => {
        console.log(this.state.userEmail);
        // debugger;
        this.setState({ submitted: true });
    };

    handleChange = (evt) => {
        this.setState({ userEmail: evt.target.value });
        console.log(this.state.userEmail)
    };

    render() {
        if (this.state.submitted) {
            return (
                <p className="infoForm-text">Благодарим вас за подписку. Мы свяжемся с вами, когда царство комфорта
                    откроется!</p>
            );
        } else {
            return (
                <div className="infoForm-container">
                    <p className="infoForm-text">Заполните форму ниже, чтобы узнать об открытии «Империи матрасов»
                        первыми.</p>
                    <form onSubmit={this.handleSubmit} className="infoForm-form">
                        <input onChange={this.handleChange} className="infoForm-input" type="email" required minLength="7"  placeholder={'Введите e-mail'} />
                        <button className="infoForm-button" type="submit">Удивите меня</button>
                    </form>
                </div>
            );
        }
    }
}

export default InfoForm;